.wcf__event-slider .image {
  aspect-ratio: 1/1;
  border-radius: 100%;
  overflow: hidden;
  line-height: 0;
}
.wcf__event-slider .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.wcf__event-slider .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  aspect-ratio: 1/1;
  background-color: #4b5573;
  border-radius: 100%;
  padding: 50px 30px;
  margin-top: -20%;
  color: #fff;
  position: relative;
  z-index: 1;
}
.wcf__event-slider .date {
  color: #999999;
  margin-bottom: 10px;
}
.wcf__event-slider .title {
  color: #fff;
  margin-bottom: 20px;
}
.wcf__event-slider .desc {
  margin-bottom: 40px;
}
@media (min-width: 992px) {
  .wcf__event-slider .slide {
    display: flex;
    align-items: center;
  }
  .wcf__event-slider .slide .content,
  .wcf__event-slider .slide .image {
    width: 55%;
  }
  .wcf__event-slider .slide .content {
    margin-top: 0;
    margin-left: -10%;
  }
}
.wcf__event-slider .wcf-arrow {
  cursor: pointer;
  color: #666666;
  position: absolute;
  width: 60px;
  height: 60px;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transform: translateY(-50%);
  transition: all 0.3s;
  z-index: 9;
}
.wcf__event-slider .wcf-arrow svg {
  width: 1em;
  height: 1em;
}
.wcf__event-slider .wcf-arrow:hover {
  color: #1C1D20;
}
.wcf__event-slider .wcf-arrow-prev {
  left: 0;
}
.wcf__event-slider .wcf-arrow-next {
  right: 0;
}
.wcf__event-slider .swiper-pagination {
  bottom: 0;
  pointer-events: none;
}
.wcf__event-slider .swiper-pagination span {
  font-size: 14px;
  color: #384364;
}
.wcf__event-slider .mid-line {
  width: 100px;
  display: inline-block;
  background-color: #384364;
  height: 1px;
  margin-right: 10px;
  margin-left: 10px;
}
.wcf__event-slider .wcf__slider {
  max-width: 950px;
  margin: 0 auto 90px;
}
@media only screen and (max-width: 1199px) {
  .wcf__event-slider .wcf__slider {
    margin-bottom: 50px;
  }
}
@media only screen and (max-width: 991px) {
  .wcf__event-slider .wcf__slider {
    gap: 30px;
    margin-bottom: 40px;
  }
}